body {
  background-color: #353535;
  font-family: 'Open Sans Condensed', sans-serif;
  .container {
    max-width: 1600px; } }

.row {
    h2 {
      color: red;
      text-shadow: 2px 2px 2px #000; }
    .title-icon {
      margin-bottom: 30px;
      color: red; } }

a {
  transition: color .2s linear 0s; }

.top-nav {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 13px;
  font-family: 'Open Sans', sans-serif;

  li {
    display: inline;
    padding: .1rem .5rem;
    border-right: 1px solid #fff;

    &:last-of-type {
      border-right: 0; }

    a {
      color: #e0e0e0;
      text-decoration: none;

      &:hover, &:focus {
        color: #fff; } } } }

.main-logo {
  margin-left: auto;
  margin-right: auto;
  margin-top: -15px;
  height: 75px; }

.header-item {
  padding: .25rem .4rem;
  cursor: pointer;
  color: #ffffff;
  transition: color .2s linear 0s;
  font-weight: bold;
  line-height: 2.5;

  img {
    max-width: 220px;
    max-height: 55px; } }

.header-item-holder .header-item {
  &:hover, &:focus {
    color: #bb0f0f;
    text-decoration: none; } }

.navbar-toggler {
  border: 1px solid #fff; }

.nav-item {
  margin-right: 5px;
  .dropdown-menu {
      margin-top: 0px;
      background-color: #bb0f0f;
      border: 1px solid #000;
      .dropdown-item {
          color: #fff;
          &:hover {
              color: #000;
              background-color: transparent; } } }

  &:last-of-type {
    margin-right: 0; }

  .nav-link {
    padding-top: .2rem;
    padding-bottom: .3rem;
    font-size: 16px;
    letter-spacing: 1px;
    color: #fff;
    text-shadow: 1px 1px 1px #000;
    &:hover {
        color: #bb0f0f; } } }

.btn-group {
  margin-left: auto;
  margin-right: auto;
  @media screen and ( min-width: 768px ) {
    margin: 0; }
  .btn-category {
    color: #fff;
    border-radius: 4px;
    background-color: transparent;
    box-shadow: 3px 3px 5px #000;
    padding: 5px 40px;
    left: auto;
    right: auto; }
  .dropdown-menu {
    background-color: #202020;
    width: 100%;
    .dropdown-item {
      text-align: center;
      @media screen and ( min-width: 768px ) {
        text-align: start; }
      &:hover {
        background-color: #525252; } }
    a {
      color: #fff; } } }


