.panel-grid-cell section {
  padding: 80px 0;
  z-index: 999;
  text-align: center; }

input {
  &[type="button"]:hover, &[type="submit"]:hover, &[type="reset"]:hover {
    background-color: #bb0f0f; } }

button:hover, .btn:hover {
  background-color: #bb0f0f; }

.mfp-close-btn-in .mfp-close {
  color: #bb0f0f !important; }

/* modal windows */

div > a.popup-modal.button[href="#form"] {
  text-transform: uppercase;
  text-decoration: none;
  margin-top: 100px; }

.mfp-content {
  //background-image: url("../images/callback_bg.jpg") !important
  //position: relative
  position: fixed;
  display: inline-block;
  vertical-align: middle;
  margin: 0 auto;
  text-align: center !important;
  background: #000000;
  max-width: 280px;
  z-index: 1045;
  height: auto;
  padding: .8%;
  border-radius: 4px; }

a.popup-modal-dismiss {
  position: absolute;
  top: 0;
  left: 0;
  overflow: visible;
  cursor: pointer;
  -webkit-transition: color 0.2s;
  transition: color 0.2s;
  text-decoration: none;
  color: #959c96;
  border: 0;
  background: transparent; }

.putNumber, .putName, .putPhone {
  width: 210px !important;
  padding: 9px 10px !important;
  height: 40px !important;
  background: rgba(75, 75, 75, 0.62) !important;
  margin-bottom: 15px;
  border-radius: 2px !important;
  border: none !important;
  outline: none !important;
  margin: 10px auto;
  font-family: Alice, serif;
  font-size: 18px; }

input {
  &.putName:focus, &.putPhone:focus {
    box-shadow: 0 0 2px 1px #bb0f0f !important; } }

#form > input.btn {
  margin-top: 10px; }

p.title {
  margin: 15px 10px 2px !important;
  color: #e1e1e1 !important;
  font-family: Marck Script, cursive !important;
  line-height: 1.4 !important;
  font-size: 22px; }

#form {
  border: 1px solid #bb0f0f;
  border-radius: 4px; }

input.btn {
  width: 210px;
  height: 40px;
  background: #bb0f0f;
  margin: 10px 0px 15px !important;
  border-radius: 2px;
  border: none;
  outline: none;
  color: #fff;
  cursor: pointer;
  line-height: 1;

  &:hover {
    color: #fff; } }
